"use client";

import React, { useState, useMemo } from "react";
import Image from "next/image";
import {
    Table,
    TableBody,
    TableCell,
    TableHeader,
    TableRow,
} from "@/components/ui/table";
import Badge from "@/components/ui/badge/Badge";
import { Modal } from "@/components/ui/modal";
import PageBreadcrumb from "@/components/common/PageBreadCrumb";
import {
    Plus,
    ArrowLeft,
    Eye,
    UploadCloud,
} from "lucide-react";

/* ---------------- TYPES ---------------- */

type ExpenseStatus = "Draft" | "Submitted" | "Approved" | "Rejected";

interface TravelExpense {
    id: number;
    tripName: string;
    travelDate: string;
    amount: number;
    status: ExpenseStatus;
    receipt?: string;
}

/* ---------------- MOCK DATA ---------------- */

const initialData: TravelExpense[] = [
    {
        id: 1,
        tripName: "Client Visit – Bangalore",
        travelDate: "2025-01-10",
        amount: 3200,
        status: "Draft",
    },
    {
        id: 2,
        tripName: "Conference – Mumbai",
        travelDate: "2025-01-05",
        amount: 8700,
        status: "Submitted",
    },
];

/* ---------------- PAGE ---------------- */

export default function TravelExpensesPage() {
    const [data, setData] = useState<TravelExpense[]>(initialData);
    const [mode, setMode] = useState<"list" | "create" | "detail">("list");
    const [selected, setSelected] = useState<TravelExpense | null>(null);

    const [form, setForm] = useState<Partial<TravelExpense>>({});
    const [preview, setPreview] = useState<string | null>(null);

    /* ---------------- FILTERS + PAGINATION ---------------- */

    const [statusFilter, setStatusFilter] = useState<ExpenseStatus | "All">(
        "All"
    );
    const [page, setPage] = useState(1);
    const pageSize = 5;

    const filteredData = useMemo(() => {
        return statusFilter === "All"
            ? data
            : data.filter((d) => d.status === statusFilter);
    }, [data, statusFilter]);

    const paginatedData = filteredData.slice(
        (page - 1) * pageSize,
        page * pageSize
    );

    /* ---------------- HANDLERS ---------------- */

    const openCreate = () => {
        setForm({
            tripName: "",
            travelDate: "",
            amount: 0,
            status: "Draft",
        });
        setMode("create");
    };

    const openDetail = (record: TravelExpense) => {
        setSelected(record);
        setForm(record);
        setMode("detail");
    };

    const saveDraft = () => {
        setData([
            { ...(form as TravelExpense), id: Date.now(), status: "Draft" },
            ...data,
        ]);
        setMode("list");
    };

    const submitExpense = () => {
        setData((prev) =>
            prev.map((r) =>
                r.id === selected?.id ? { ...r, status: "Submitted" } : r
            )
        );
        setMode("list");
    };

    /* ---------------- UI ---------------- */

    return (
        <div className="p-6 space-y-6">
            <PageBreadcrumb pageTitle="Travel Expenses" />

            {/* ---------------- LIST ---------------- */}
            {mode === "list" && (
                <>
                    {/* Toolbar */}
                    <div className="flex justify-between items-center">
                        <div className="flex gap-2">
                            <select
                                className="border rounded px-3 py-2"
                                value={statusFilter}
                                onChange={(e) =>
                                    setStatusFilter(e.target.value as ExpenseStatus | "All")
                                }
                            >
                                <option value="All">All Status</option>
                                <option value="Draft">Draft</option>
                                <option value="Submitted">Submitted</option>
                                <option value="Approved">Approved</option>
                                <option value="Rejected">Rejected</option>
                            </select>
                        </div>

                        <button
                            onClick={openCreate}
                            className="flex items-center gap-2 bg-brand-500 text-white px-4 py-2 rounded"
                        >
                            <Plus size={16} /> New Travel Expense
                        </button>
                    </div>

                    {/* Table */}
                    <div className="overflow-hidden rounded-xl border bg-white">
                        <div className="max-w-full overflow-x-auto">
                            <Table>
                                <TableHeader>
                                    <TableRow>
                                        <TableCell isHeader className="text-left px-5 py-3">
                                            Trip
                                        </TableCell>
                                        <TableCell isHeader className="text-left px-5 py-3">
                                            Date
                                        </TableCell>
                                        <TableCell isHeader className="text-left px-5 py-3">
                                            Amount
                                        </TableCell>
                                        <TableCell isHeader className="text-left px-5 py-3">
                                            Status
                                        </TableCell>
                                        <TableCell isHeader className="text-left px-5 py-3">
                                            Action
                                        </TableCell>
                                    </TableRow>
                                </TableHeader>

                                <TableBody>
                                    {paginatedData.map((r) => (
                                        <TableRow key={r.id}>
                                            <TableCell className="px-5 py-4 font-medium">
                                                {r.tripName}
                                            </TableCell>
                                            <TableCell className="px-5 py-4">
                                                {r.travelDate}
                                            </TableCell>
                                            <TableCell className="px-5 py-4">
                                                ₹{r.amount}
                                            </TableCell>
                                            <TableCell className="px-5 py-4">
                                                <Badge
                                                    size="sm"
                                                    color={
                                                        r.status === "Approved"
                                                            ? "success"
                                                            : r.status === "Submitted"
                                                                ? "warning"
                                                                : r.status === "Rejected"
                                                                    ? "error"
                                                                    : "secondary"
                                                    }
                                                >
                                                    {r.status}
                                                </Badge>
                                            </TableCell>
                                            <TableCell className="px-5 py-4">
                                                <button
                                                    onClick={() => openDetail(r)}
                                                    className="flex items-center gap-1 text-brand-500"
                                                >
                                                    <Eye size={16} /> View
                                                </button>
                                            </TableCell>
                                        </TableRow>
                                    ))}
                                </TableBody>
                            </Table>
                        </div>
                    </div>

                    {/* Pagination */}
                    <div className="flex justify-center gap-2">
                        {Array.from({
                            length: Math.ceil(filteredData.length / pageSize),
                        }).map((_, i) => (
                            <button
                                key={i}
                                onClick={() => setPage(i + 1)}
                                className={`px-3 py-1 rounded border ${page === i + 1 ? "bg-brand-500 text-white" : ""
                                    }`}
                            >
                                {i + 1}
                            </button>
                        ))}
                    </div>
                </>
            )}

            {/* ---------------- CREATE / DETAIL ---------------- */}
            {(mode === "create" || mode === "detail") && (
                <div className="max-w-xl mx-auto bg-white border rounded-xl p-6 space-y-4">
                    <button
                        onClick={() => setMode("list")}
                        className="flex items-center gap-2 text-gray-500"
                    >
                        <ArrowLeft size={16} /> Back to list
                    </button>

                    <h2 className="text-xl font-semibold">
                        {mode === "create" ? "New Travel Expense" : "Travel Expense"}
                    </h2>

                    <input
                        placeholder="Trip Name"
                        className="w-full border rounded px-3 py-2"
                        value={form.tripName || ""}
                        disabled={selected?.status !== "Draft"}
                        onChange={(e) =>
                            setForm({ ...form, tripName: e.target.value })
                        }
                    />

                    <input
                        type="date"
                        className="w-full border rounded px-3 py-2"
                        value={form.travelDate || ""}
                        disabled={selected?.status !== "Draft"}
                        onChange={(e) =>
                            setForm({ ...form, travelDate: e.target.value })
                        }
                    />

                    <input
                        type="number"
                        className="w-full border rounded px-3 py-2"
                        value={form.amount || 0}
                        disabled={selected?.status !== "Draft"}
                        onChange={(e) =>
                            setForm({ ...form, amount: Number(e.target.value) })
                        }
                    />

                    {/* Receipt Upload */}
                    {selected?.status === "Draft" && (
                        <label className="flex items-center gap-2 cursor-pointer text-brand-500">
                            <UploadCloud size={18} />
                            Upload Receipt
                            <input
                                type="file"
                                hidden
                                onChange={(e) =>
                                    e.target.files &&
                                    setPreview(URL.createObjectURL(e.target.files[0]))
                                }
                            />
                        </label>
                    )}

                    {preview && (
                        <Image
                            src={preview}
                            alt="Receipt"
                            width={200}
                            height={200}
                            className="rounded border"
                            onClick={() => setPreview(preview)}
                        />
                    )}

                    <div className="flex justify-end gap-2">
                        {mode === "create" && (
                            <button
                                onClick={saveDraft}
                                className="bg-brand-500 text-white px-4 py-2 rounded"
                            >
                                Save Draft
                            </button>
                        )}

                        {selected?.status === "Draft" && (
                            <button
                                onClick={submitExpense}
                                className="bg-success-500 text-white px-4 py-2 rounded"
                            >
                                Submit
                            </button>
                        )}
                    </div>
                </div>
            )}
        </div>
    );
}
